.product-detail-list {
    list-style-type: none;
    padding: 10px;
    min-width: 90%;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.product-detail-item {
    font-size: .85rem;
    column-gap: 30px;
    display: flex;
    justify-content: space-between;
    text-align: end;
    text-overflow: clip;
    align-items: center;
}

.product-detail {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.product-detail-title {
    width: 120px;
    text-align: start;
}